/**
 * @usedBy PoChartComponent
 *
 * @description
 *
 * *Enum* `PoChartType` para especificao dos tipos de grficos.
 */
export enum PoChartType {
  /**
   * Tipo de grfico que exibe os dados de modo quantitativo, utilizando linhas contnuas demarcadas por pontos para cada valor de srie definido.
   * Similar ao grfico de linha, diferencia-se pela rea localizada abaixo da linha das sries, que  preenchida com cores para um destaque explcita da evoluo e mudana dos dados.
   */
  Area = 'area',

  /**
   * Exibe os dados em formato de rosca, dividindo em partes proporcionais.
   */
  Donut = 'donut',

  /**
   * @deprecated 6.x.x
   *
   * @description
   *
   * **Deprecated 6.x.x**
   *
   * O grfico de *gauge* fornece como opo uma melhor relao de intensidade de dados que nos grficos de pizza padro ou rosca, uma vez
   * que o centro em branco pode ser usado para exibir dados adicionais relacionados.
   *
   * > Componente depreciado, por favor utilizar componente [po-gauge](/documentation/po-gauge).
   */
  Gauge = 'gauge',

  /**
   * Exibe os dados em formato circular, dividindo proporcionalmente em fatias.
   */
  Pie = 'pie',

  /**
   * Grfico que mostra os dados de modo linear e contnuo.  til, por exemplo, para fazer comparaes de tendncia durante determinado perodo.
   * Pode ser utilizado em conjunto com grficos dos tipos `column` e `area`, definindo-se o tipo atravs da propriedade `PoChartSerie.type`.
   */
  Line = 'line',

  /**
   * Grfico que exibe os dados em forma de barras verticais e sua extenso varia de acordo com seus valores.  comumente usado como comparativo entre diversas sries.
   * As sries so exibidas lado-a-lado, com um pequeno espao entre elas.
   */
  Column = 'column',

  /**
   * Grfico que exibe os dados em forma de barras horizontais e sua extenso varia de acordo com seus valores.  comumente usado como comparativo de sries e categorias.
   */
  Bar = 'bar'
}
