import { Input, Directive, TemplateRef } from '@angular/core';

import { convertToInt } from '../../utils/util';

import { PoToolbarAction } from './po-toolbar-action.interface';
import { PoToolbarProfile } from './po-toolbar-profile/po-toolbar-profile.interface';

/**
 * @description
 *
 * O componente `po-toolbar`  um cabealho para o ttulo da aplicao e informaes de usurio e notificaes quando houver necessidade.
 */
@Directive()
export class PoToolbarBaseComponent {
  /**
   * @optional
   *
   * @description
   *
   * Define uma lista de aes que sero exibidas ao clicar no cone declarado em `p-actions-icon`.
   */
  @Input('p-actions') actions?: Array<PoToolbarAction>;

  /**
   * @optional
   *
   * @description
   *
   * Define um [cone](/guides/icons) para a propriedade `p-actions`.
   *
   *  possvel usar qualquer um dos cones da [Biblioteca de cones](/guides/icons). conforme exemplo abaixo:
   * ```
   * <po-toolbar p-actions-icon="po-icon-user" [p-actions]="actions"></po-toolbar>
   * ```
   * Tambm  possvel utilizar outras fontes de cones, por exemplo a biblioteca *Font Awesome*, da seguinte forma:
   * ```
   * <po-toolbar p-actions-icon="far fa-comment-alt" [p-actions]="actions"></po-toolbar>
   * ```
   * Outra opo seria a customizao do cone atravs do `TemplateRef`, conforme exemplo abaixo:
   * ```
   * <po-toolbar [p-actions-icon]="template" [p-actions]="actions"></po-toolbar>
   *
   * <ng-template #template>
   *  <ion-icon style="font-size: inherit" name="heart"></ion-icon>
   * </ng-template>
   * ```
   * > Para o cone enquadrar corretamente, deve-se utilizar `font-size: inherit` caso o cone utilizado no aplique-o.
   *
   * > Caso no haja aes definidas em `p-actions`, o cone no ser exibido.
   *
   * @default `po-icon-more`
   */
  @Input('p-actions-icon') actionsIcon?: string | TemplateRef<void>;

  /** Define o objeto que ser o cabealho da lista de aes com as informaes do perfil. */
  @Input('p-profile') profile?: PoToolbarProfile;

  /** Define uma lista de aes que sero exibidas ao clicar no cone do perfil. */
  @Input('p-profile-actions') profileActions?: Array<PoToolbarAction>;

  /** Se falso, oculta o cone de notificaes. */
  @Input('p-show-notification') showNotification?: boolean = true;

  /** Ttulo do *toolbar* e aplicao. */
  @Input('p-title') title: string;

  /**
   * @optional
   *
   * @description
   *
   * Lista de aes da notificao.
   */
  @Input('p-notification-actions') notificationActions?: Array<PoToolbarAction>;

  private _notificationNumber?: number;

  /**
   * @optional
   *
   * @description
   *
   * Nmero de notificaes.
   */
  @Input('p-notification-number') set notificationNumber(value: number) {
    this._notificationNumber = convertToInt(value, 0);
  }

  get notificationNumber(): number {
    return this._notificationNumber;
  }

  get isShowProfile(): boolean {
    return !!(this.profile || this.profileActions);
  }
}
