import { EventEmitter, Input, Output, Directive, TemplateRef, ChangeDetectorRef } from '@angular/core';
import { AbstractControl, ControlValueAccessor, Validator } from '@angular/forms';

import { convertToBoolean } from '../../../utils/util';
import { requiredFailed, maxlengpoailed, minlengpoailed, patternFailed } from './../validators';
import { InputBoolean } from '../../../decorators';
import { PoMask } from './po-mask';

/**
 * @description
 *
 * Este  um componente baseado em input, com vrias propriedades do input nativo e outras
 * propriedades extras como: mscara, pattern, mensagem de erro e etc.
 * Voc deve informar a varivel que contm o valor como [(ngModel)]="variavel", para que o
 * input receba o valor da varivel e para que ela receba as alteraes do valor (two-way-databinding).
 * A propriedade name  obrigatria para que o formulrio e o model funcionem corretamente.
 *
 * Importante:
 *
 * - Caso o input tenha um [(ngModel)] sem o atributo name, ocorrer um erro de angular.
 * Ento voc precisa informar o atributo name ou o atributo [ngModelOptions]="{standalone: true}".
 * Exemplo: [(ngModel)]="pessoa.nome" [ngModelOptions]="{standalone: true}".
 *
 */
@Directive()
export abstract class PoInputBaseComponent implements ControlValueAccessor, Validator {
  /**
   * @optional
   *
   * @description
   *
   * Aplica foco no elemento ao ser iniciado.
   *
   * > Caso mais de um elemento seja configurado com essa propriedade, apenas o ltimo elemento declarado com ela ter o foco.
   *
   * @default `false`
   */
  @Input('p-auto-focus') @InputBoolean() autoFocus: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define o cone que ser exibido no incio do campo.
   *
   *  possvel usar qualquer um dos cones da [Biblioteca de cones](/guides/icons). conforme exemplo abaixo:
   * ```
   * <po-input p-icon="po-icon-user" p-label="PO input"></po-input>
   * ```
   * Tambm  possvel utilizar outras fontes de cones, por exemplo a biblioteca *Font Awesome*, da seguinte forma:
   * ```
   * <po-input p-icon="fa fa-podcast" p-label="PO input"></po-input>
   * ```
   * Outra opo seria a customizao do cone atravs do `TemplateRef`, conforme exemplo abaixo:
   * ```
   * <po-input [p-icon]="template" p-label="input template ionic"></po-input>
   *
   * <ng-template #template>
   *  <ion-icon style="font-size: inherit" name="heart"></ion-icon>
   * </ng-template>
   * ```
   * > Para o cone enquadrar corretamente, deve-se utilizar `font-size: inherit` caso o cone utilizado no aplique-o.
   */
  @Input('p-icon') icon?: string | TemplateRef<void>;

  /** Rtulo do campo. */
  @Input('p-label') label?: string;

  /** Texto de apoio do campo. */
  @Input('p-help') help?: string;

  /** Nome e identificador do campo. */
  @Input('name') name: string;

  /**
   * @description
   *
   * Mensagem que ser apresentada quando o `pattern` ou a mscara no for satisfeita.
   *
   * > Esta mensagem no  apresentada quando o campo estiver vazio, mesmo que ele seja requerido.
   */
  @Input('p-error-pattern') errorPattern?: string = '';

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   * - O campo conter `p-required`;
   * - No possuir `p-help` e/ou `p-label`.
   *
   * @default `false`
   */
  @Input('p-optional') optional: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao sair do campo.
   */
  @Output('p-blur') blur: EventEmitter<any> = new EventEmitter();

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao entrar do campo.
   */
  @Output('p-enter') enter: EventEmitter<any> = new EventEmitter();

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao alterar valor e deixar o campo.
   */
  @Output('p-change') change: EventEmitter<any> = new EventEmitter();

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao alterar valor do model.
   */
  @Output('p-change-model') changeModel: EventEmitter<any> = new EventEmitter();

  type: string;

  onChangePropagate: any = null;
  objMask: any;
  modelLastUpdate: any;
  protected onTouched: any = null;

  protected passedWriteValue: boolean = false;
  protected validatorChange: any;

  private _maxlength?: number;
  private _minlength?: number;
  private _noAutocomplete?: boolean = false;
  private _placeholder?: string = '';

  /**
   * @optional
   *
   * @description
   *
   * Define a propriedade nativa `autocomplete` do campo como `off`.
   *
   * > No componente `po-password` ser definido como `new-password`.
   *
   * @default `false`
   */
  @Input('p-no-autocomplete') set noAutocomplete(value: boolean) {
    this._noAutocomplete = convertToBoolean(value);
  }

  get noAutocomplete() {
    return this._noAutocomplete;
  }

  /**
   * @optional
   *
   * @description
   *
   * Mensagem que aparecer enquanto o campo no estiver preenchido.
   *
   * @default ''
   */
  @Input('p-placeholder') set placeholder(value: string) {
    this._placeholder = value || '';
  }
  get placeholder() {
    return this._placeholder;
  }

  /**
   * @description
   *
   * Se verdadeiro, desabilita o campo.
   *
   * @default `false`
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  disabled?: boolean = false;
  @Input('p-disabled') set setDisabled(disabled: string) {
    this.disabled = disabled === '' ? true : convertToBoolean(disabled);

    this.validateModel();
  }

  /** Indica que o campo ser somente leitura. */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  readonly?: boolean = false;
  @Input('p-readonly') set setReadonly(readonly: string) {
    this.readonly = readonly === '' ? true : convertToBoolean(readonly);
  }

  /**
   * @description
   *
   * Indica que o campo ser obrigatrio.
   *
   * > Esta propriedade  desconsiderada quando o input est desabilitado `(p-disabled)`.
   *
   * @default `false`
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  required?: boolean = false;
  @Input('p-required') set setRequired(required: string) {
    this.required = required === '' ? true : convertToBoolean(required);

    this.validateModel();
  }

  /** Se verdadeiro, o campo receber um boto para ser limpo. */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  clean?: boolean = false;
  @Input('p-clean') set setClean(clean: string) {
    this.clean = clean === '' ? true : convertToBoolean(clean);
  }

  /**
   * @description
   *
   * Expresso regular para validar o campo.
   * Quando o campo possuir uma mscara `(p-mask)` ser automaticamente validado por ela, porm
   *  possvel definir um p-pattern para substituir a validao da mscara.
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  pattern?: string;
  @Input('p-pattern') set setPattern(pattern: string) {
    this.pattern = pattern;

    this.validateModel();
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica a quantidade mxima de caracteres que o campo aceita.
   */
  @Input('p-maxlength') set maxlength(value: number) {
    if (!isNaN(parseInt(<any>value, 10))) {
      this._maxlength = parseInt(<any>value, 10);

      this.validateModel();
    } else if (!value) {
      this._maxlength = undefined;

      this.validateModel();
    }
  }

  get maxlength() {
    return this._maxlength;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica a quantidade mnima de caracteres que o campo aceita.
   */
  @Input('p-minlength') set minlength(value: number) {
    if (!isNaN(parseInt(<any>value, 10))) {
      this._minlength = parseInt(<any>value, 10);

      this.validateModel();
    } else if (!value) {
      this._minlength = undefined;

      this.validateModel();
    }
  }

  get minlength() {
    return this._minlength;
  }

  /**
   * @description
   *
   * Indica uma mscara para o campo. Exemplos: (+99) (99) 99999?-9999, 99999-999, 999.999.999-99.
   * A mscara gera uma validao automtica do campo, podendo esta ser substituda por um REGEX especfico
   * atravs da propriedade p-pattern.
   * O campo ser sinalizado e o formulrio ficar invlido quando o valor informado estiver fora do padro definido,
   * mesmo quando desabilitado.
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  mask?: string = '';
  @Input('p-mask') set setMask(mask: string) {
    this.mask = mask;

    // Atualiza Mscara do Campo
    this.objMask = new PoMask(this.mask, this.maskFormatModel);
  }

  /**
   * @description
   *
   * Indica se o `model` receber o valor formatado pela mscara ou apenas o valor puro (sem formatao).
   *
   * @default `false`
   */
  // eslint-disable-next-line @typescript-eslint/member-ordering
  maskFormatModel?: boolean = false;
  @Input('p-mask-format-model') set setMaskFormatModel(maskFormatModel: string) {
    this.maskFormatModel = maskFormatModel === '' ? true : convertToBoolean(maskFormatModel);

    if (this.objMask instanceof PoMask) {
      this.objMask.formatModel = this.maskFormatModel;

      this.validateModel();
    }
  }

  constructor(private cd?: ChangeDetectorRef) {}

  callOnChange(value: any) {
    this.updateModel(value);

    this.controlChangeModelEmitter(value);
  }

  callUpdateModelWithTimeout(value) {
    setTimeout(() => this.updateModel(value));
  }

  controlChangeModelEmitter(value: any) {
    if (this.modelLastUpdate !== value) {
      this.changeModel.emit(value);
      this.modelLastUpdate = value;
    }
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar os estados de habilitado via forms api
  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
    this.cd?.markForCheck();
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnChange(func: any): void {
    this.onChangePropagate = func;
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnTouched(func: any): void {
    this.onTouched = func;
  }

  registerOnValidatorChange(fn: () => void) {
    this.validatorChange = fn;
  }

  updateModel(value: any) {
    // Quando o input no possui um formulrio, ento esta funo no  registrada
    if (this.onChangePropagate) {
      this.onChangePropagate(value);
    }
  }

  validate(c: AbstractControl): { [key: string]: any } {
    if (requiredFailed(this.required, this.disabled, this.getScreenValue())) {
      return {
        required: {
          valid: false
        }
      };
    }

    if (maxlengpoailed(this.maxlength, this.getScreenValue())) {
      return {
        maxlength: {
          valid: false
        }
      };
    }

    if (minlengpoailed(this.minlength, this.getScreenValue())) {
      return {
        minlength: {
          valid: false
        }
      };
    }

    if (patternFailed(this.pattern, c.value)) {
      this.validatePatternOnWriteValue(c.value);
      return {
        pattern: {
          valid: false
        }
      };
    }

    return this.extraValidation(c);
  }

  // Funo implementada do ControlValueAccessor
  writeValue(value: any) {
    this.writeValueModel(value);
    this.cd?.markForCheck();
  }

  protected validateModel() {
    if (this.validatorChange) {
      this.validatorChange();
    }
  }

  // utilizado para validar o pattern na inicializacao, fazendo dessa forma o campo fica sujo (dirty).
  private validatePatternOnWriteValue(value: string) {
    if (value && this.passedWriteValue) {
      setTimeout(() => {
        this.updateModel(value);
      });

      this.passedWriteValue = false;
    }
  }

  /**
   * Funo que atribui foco ao componente.
   *
   * Para utiliz-la  necessrio ter a instncia do componente no DOM, podendo ser utilizado o ViewChild da seguinte forma:
   *
   * ```
   * import { PoNomeDoComponenteComponent } from '@po-ui/ng-components';
   *
   * ...
   *
   * @ViewChild(PoNomeDoComponenteComponent, { static: true }) nomeDoComponente: PoNomeDoComponenteComponent;
   *
   * focusComponent() {
   *   this.nomeDoComponente.focus();
   * }
   * ```
   */
  abstract focus(): void;

  // Mtodo que receber o valor do model
  abstract writeValueModel(value: any): void;

  // Validaes do campo
  abstract extraValidation(c: AbstractControl): { [key: string]: any };

  // Deve retornar o valor do campo
  abstract getScreenValue(): string;
}
