// Padding padro
export const PoChartPadding = 24;

// rea lateral designada para os rtulos do eixo X
export const PoChartAxisXLabelArea = 56;

// Quantidade de linhas do eixo X
export const PoChartGridLines = 5;

// Padding top para rea interna de plotagem do grid para evitar overflow no hover dos pontos do grfico do tipo linha;
export const PoChartPlotAreaPaddingTop = 8;

// Angulao inicial de raio para grficos do tipo circular
export const PoChartStartAngle = -Math.PI / 2;

// Valor referente  espessura padro do grfico do tipo Donut.
export const PoChartDonutDefaultThickness = 40;

// Valor para subtrao do valor de angulo radiano final de srie em tipos Donut e Pie. Necessrio para o caso de uma srie nica: se uma circunferncia tiver valores de ngulo de raio inicial e final iguais no plota.
export const PoChartCompleteCircle = 0.0001;

// Durao da animao para grficos dos tipos Donut e Pie.
const durationTime = 1500;
export const PoChartAngleStepInterval = (Math.PI * 2) / Math.floor(durationTime / 60);
